import { AuthContext } from "../contexts/auth_context"
import { getApiLink } from "../lib/env.lib"
const formatDate = (ts) => {
    let dt = new Date(ts)
    let day = dt.getDay()
    let date = dt.getDate()
    let month = dt.getMonth()
    let year = dt.getFullYear()
    let week = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"]
    let mon = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Now", "Dec"]
    return `${week[day]} ${date} ${mon[month]} ${year}`
}

const getStringCount = (i) => {
    let ctr = 1000000000
    let s = i + ""
    let j = 0
    let val = ["B", "M", "K"]
    while (j < 3) {
        if (i > ctr) {
            let c = i / ctr
            let h = Math.floor(c)
            if (c !== h) {
                if (i % (ctr / 100) === 0) {
                    h = c.toFixed(1)
                } else {
                    h = c.toFixed(2)
                }
            }
            s = `${h}${val[j]}`
            break
        }
        ctr = ctr / 1000
        j++
    }
    return s
}

const setLikeDisp = (liked) => {
    let likeDisp
    if (!liked) {
        likeDisp = (
            <LikeThin className="action-icon" />
        )
    } else {
        likeDisp = (
            <Like className="action-icon liked" />
        )
    }

    return likeDisp
}

function setBookmarkDisp(bookmarked) {
    let bkDisp
    if (!bookmarked) {
        bkDisp = (
            <BookmarkThin className="action-icon" />
        )
    } else {
        bkDisp = (
            <Bookmark className="action-icon bookmarked" />
        )
    }

    return bkDisp
}

const deleteArticle = (token, id, cb) => {
    if (id) {
        Axios.delete(`${getApiLink()}/api/v1/deleteArticleById`, {
            data: {
                xAuthToken: token,
                article_id: id
            }
        }).then(response => {
            cb()
        }).catch(e => {
            console.log(e.response.data.error, { token, id })
        })
    }
}

const likeArticle = (token, id, like, cb) => {
    if (id && token !== "") {
        Axios.post(`${getApiLink()}/api/v1/likeArticle`, {
            xAuthToken: token,
            article_id: id,
            action: (like) ? 1 : 0
        }).then(response => {
            // console.log("Liked article")
            cb()
        }).catch(e => {
            console.log(e.response.data.error, { token, id })
        })
    }
}

const bookmarkArticle = (token, id, bookmark, cb) => {
    if (id && token !== "") {
        Axios.post(`${getApiLink()}/api/v1/bookmarkArticle`, {
            xAuthToken: token,
            article_id: id,
            action: (bookmark) ? 1 : 0
        }).then(response => {
            // console.log("Liked article")
            cb()
        }).catch(e => {
            console.log(e.response.data.error, { token, id })
        })
    }
}

const UserDetails = (props) => {

    const [liked, setLiked] = useState(false)
    const [bookmarked, setBookmarked] = useState(false)

    const [modalDisp, setModalDisp] = useState(false)

    // console.log({ p_from_card: props })

    useEffect(() => {

        if (props.data.liked) {
            setLiked(true)
        }

        if (props.data.bookmarked) {
            setBookmarked(true)
        }
    }, [props.data])

    const { user_id, auth_token } = useContext(AuthContext)

    let bookmarkDisp = setBookmarkDisp(bookmarked)

    let likeDisp = setLikeDisp(liked)

    function handleLike() {
        let l = liked
        likeDisp = setLikeDisp(!l)
        if (auth_token !== "")
            likeArticle(auth_token, props.data.article_id, !l, () => {
                setLiked(!l)
            })
        else
            console.log("Log in to like")

    }

    function handleBookmark(e) {
        let b = bookmarked
        bookmarkDisp = setBookmarkDisp(!b)
        if (auth_token !== "")
            bookmarkArticle(auth_token, props.data.article_id, !b, () => {
                setBookmarked(!b)
            })
        else
            console.log("Log in to like")
    }



    return (
        <>
            <div className="user-info-c pl-2 pr-2 pb-2 pl-sm-2 pl-md-3 pl-lg-3 pr-sm-2 pr-md-3 pr-lg-3">
                <div className="user-image-c hide-xs_x show-sm_x pr-2">
                    <Link className="unset" to={`/@${props.data.user.username}`}>
                        <img src={props.data.user.imageLink} alt="user" className="user-image" style={{
                            backgroundColor: "white"
                        }} />
                    </Link>
                </div>

                <div className="user-info">
                    <Link className="unset" to={`/@${props.data.user.username}`}>
                        <span className="u-name d-block">
                            <span className="hide-xs hide-sm show-md">
                                {props.data.user.name}
                            </span>
                            <span className="hide-xs show-sm hide-md">
                                {(props.data.user.name.length > 16) ? props.data.user.name.slice(0, 16) + "..." : props.data.user.name}
                            </span>

                            <span className="show-xs hide-sm hide-md">
                                {(props.data.user.name.length > 10) ? props.data.user.name.slice(0, 10) + "..." : props.data.user.name}
                            </span>
                        </span>
                        <span className="u-username d-inline-block">
                            <span className="hide-xs hide-sm show-md">
                                @{props.data.user.username}
                            </span>
                            <span className="hide-xs show-sm hide-md">
                                @{(props.data.user.username.length > 16) ? props.data.user.username.slice(0, 16) + "..." : props.data.user.username}
                            </span>

                            <span className="show-xs hide-sm hide-md">
                                @{(props.data.user.username.length > 12) ? props.data.user.username.slice(0, 12) + "..." : props.data.user.username}
                            </span>

                        </span>
                    </Link>
                </div>

                <div className="ml-auto u-actions">
                    <span className="action-icon-c text-center_x mr-4x " onClick={handleLike}>
                        {likeDisp}<span className="like-count noselect"> {(props.data.likeCount > 0) ? getStringCount(props.data.likeCount) : null}</span>
                    </span>

                    <span className="action-icon-c text-center_x">
                        <Link className="unset" to={`/read/${props.data.article_id}#comments`}>
                            <Comment className="action-icon" />
                        </Link>
                    </span>

                    <span className="action-icon-c text-center_x" onClick={handleBookmark}>
                        {bookmarkDisp}
                    </span>

                    {/* <span className="action-icon-c text-center_x">
                        <FontAwesomeIcon icon={faEllipsisV} className="action-icon" />
                    </span> */}




                    {(() => {
                        if (user_id === props.data.user.user_id) {
                            return (
                                <>
                                    <DropdownMenu title={<Menu className="action-icon" />}
                                        autoClose={true}
                                        className="drop-btn dropdown-toggle_x btn_x"
                                        containerClassName="action-icon-c last mr-4x"
                                        containerStyle={{
                                            // marginRight: "4fr"
                                        }}
                                        menuStyle={{
                                            transform: (window.innerWidth <= 576) ? "translateX(-80%)" : null
                                        }}>
                                        {/* <DropdownItem type="link" to="/settings">
                                            Settings
                                        </DropdownItem> */}
                                        {/* <hr className="light p-0 m-0" /> */}

                                        <DropdownItem icon={<CreateRounded />} type="link" to={`/edit/${props.data.article_id}`}>Edit</DropdownItem>
                                        <hr className="light p-0 m-0" />
                                        <DropdownItem className="logout" icon={<DeleteRounded />} onClick={(() => { setModalDisp(true) })}>Delete</DropdownItem>
                                    </DropdownMenu>

                                    <Modal show={modalDisp}
                                        onHide={(() => { setModalDisp(false) })}
                                    >
                                        <Modal.Header closeButton>
                                            <Modal.Title>Delete Article</Modal.Title>
                                        </Modal.Header>
                                        <Modal.Body>
                                            Are you sure you want to delete this article?
                                        <br />
                                        </Modal.Body>
                                        <Modal.Footer>
                                            <button className="btn btn-secondary" onClick={(() => { setModalDisp(false) })}>Close</button>
                                            <button className="btn btn-danger" onClick={
                                                () => {
                                                    deleteArticle(auth_token, props.data.article_id, () => {
                                                        props.hideCard()
                                                    })
                                                }
                                            }>Delete</button>
                                        </Modal.Footer>
                                    </Modal>
                                </>
                            )
                        }
                        else {
                            return (<>
                                {/* <DropdownItem>Report</DropdownItem> */}
                                {/* <div className="action-icon-c last"></div> */}
                                <span className="noselect">
                                    &nbsp;&nbsp;&nbsp;&nbsp;
                                </span>
                            </>)
                        }
                    })()}





                    {/* <Dropdown className="action-icon-c last text-center_x mr-4">
                        <Dropdown.Toggle className="drop-btn" variant="info">
                            <FontAwesomeIcon icon={faEllipsisV} className="action-icon nav-btn" />
                        </Dropdown.Toggle>

                        <Dropdown.Menu>
                            {(() => {
                                if (user_id === props.data.user.user_id) {
                                    return (<>
                                        <Dropdown.Item as={Link} to={`/edit/${props.data.article_id}`}>Edit</Dropdown.Item>
                                    </>)
                                } else {
                                    return (<>
                                        <Dropdown.Item eventKey="2">Report</Dropdown.Item>
                                    </>)
                                }
                            })()}


                        </Dropdown.Menu>
                    </Dropdown> */}
                </div>
            </div>
        </>
    )
}

const Card = (props) => {
    // console.log(props)

    const [hideMode, setHideMode] = useState(false)
    function trimContent(c) {
        if (c) {
            if (c.length > 200)
                c = c.slice(0, 200) + "..."
        } else {
            return ""
        }

        return c
    }

    let style = {
        display: "none"
    }
    // }
    if (props.data) {
        if (props.data.bannerLink !== null && props.data.bannerLink !== undefined && props.data.bannerLink !== "NA") {
            style = {
                backgroundImage: `url(${props.data.bannerLink})`,
                backgroundSize: "cover",
                // backgroundPosition: "50% 50%",
                // backgroundOverflow: "visible",
                backgroundPosition: "50% 50%",
            }
        }
    }


    if (props.type >= 1 && props.type <= 3) {
        if (hideMode) {
            return (<></>)
        } else
            return (
                <>
                    <div className="custom-card2 mb-3">

                        <div className="row">

                            <div className={(() => {
                                if (props.data.bannerLink !== undefined && props.data.bannerLink !== null && props.data.bannerLink !== "NA")
                                    return "col-md-9 col-lg-9"
                                else
                                    return "col-md-12"
                            })()}
                            >
                                <Link className="unset" style={{
                                    color: "unset"
                                }} to={`/read/${props.data.article_id}`}>

                                    <div style={{
                                        cursor: "pointer"
                                    }}
                                        className={(() => {
                                            if (props.data.bannerLink !== undefined && props.data.bannerLink !== null && props.data.bannerLink !== "NA")
                                                return "pl-2 pl-sm-2 pl-md-3 pl-lg-3"
                                            else
                                                return "pl-2 pr-2 pl-md-3 pl-lg-3 pr-md-3 pr-lg-3"
                                        })()}>
                                        <div className="article-row">
                                            <div className="pt-3 pb-2">
                                                <div className="article-date">
                                                    {formatDate(props.data.time)}
                                                </div>
                                                <div className="article-title">
                                                    {parser(props.data.title)}
                                                </div>
                                            </div>
                                            <div className="article-image show-xs show-sm hide-md">
                                                <div className="img preload" style={style}></div>
                                            </div>
                                        </div>


                                        <div className="article-content pb-2" style={{
                                            // maxHeight: "80px",
                                            // overflow: "hidden",
                                            // textOverflow: "ellipsis"
                                        }}>
                                            {trimContent(parser(props.data.content.split("<br>").join("")))}
                                        </div>
                                    </div>
                                </Link>

                                <div className="tags pl-2 pl-sm-2 pl-md-3 pl-lg-3" style={{
                                    paddingBottom: "3px",
                                }}>
                                    {(() => {
                                        if (props.data.tags)
                                            return props.data.tags.map((i, k_index) => {
                                                let h = hexStrColor(i)
                                                let inv = invertColor(h, true)

                                                return (
                                                    <div
                                                        key={(() => "c-tag-" + k_index)()}
                                                        className="tag d-inline-block noselect"
                                                        style={{
                                                            backgroundColor: "#" + h,
                                                            color: inv,
                                                            cursor: "pointer"
                                                        }}
                                                    >
                                                        #{i}
                                                    </div>
                                                )
                                            })
                                    })()}

                                </div>
                                {/* <div className={(() => {
                                if (props.data.bannerLink !== undefined && props.data.bannerLink !== null && props.data.bannerLink !== "NA")
                                    return "pl-2 pb-2"
                                else
                                    return "pl-4 pr-42 pb-2"
                            })()}> */}
                                <UserDetails data={props.data} hideCard={(() => { setHideMode(true) })} />
                                {/* </div> */}
                            </div>

                            <div className={(() => {
                                if (props.data.bannerLink !== undefined && props.data.bannerLink !== null && props.data.bannerLink !== "NA")
                                    return "col-md-3 col-lg-3"
                                else
                                    return "col-md-12"
                            })()}>
                                <div className="img hide-xs hide-sm show-md preload" style={{
                                    ...style,
                                    backgroundRepeat: "no-repeat",
                                    height: "100%",
                                    borderRadius: "8px"
                                }}></div>
                            </div>

                        </div>

                    </div>
                    {/* <hr className="light" /> */}
                </>
            )
    } else if (props.type === -1) {
        return (<></>)
    } else {
        return (
            <>
                <div className="card-loader w-100 text-center">
                    <br />
                    <br />
                    <br />
                    <Loader className="loader" />
                    <br />
                    <br />
                    <br />
                </div>
                {/* <hr className="light"/> */}
            </>
        )
    }
}

export default Card