import React, { useState, useEffect, useCallback, useContext } from 'react'
import Axios from "axios"
import { Waypoint } from 'react-waypoint';
import { getApiLink } from "../lib/env.lib"
import Card from "./card2"
import { ReactComponent as Loader } from "../images/loader.svg"

import { AuthContext } from '../contexts/auth_context'
const LayoutLoader = (props) => {

    const pageLimit = 10;

    const { user_id } = useContext(AuthContext)

    const [initWaypoint, setInitWaypoint] = useState(false)
    const [isLoading, setLoading] = useState(false)
    const [noNewArticle, setNoNewArticle] = useState(false)
    const [lastLoad, setLastLoad] = useState(0)
    const [stopLoding, setStopLoding] = useState(false)
    const [page, setPage] = useState(0)
    const [articles, setArticles] = useState([<Card key={"first-ele-loader-l"} className="mb-4 pl-0" type={4} />])


    const getArticle = useCallback(() => {
        let ui = []
        if (page > 0)
            setLoading(true)

        Axios.get(`${getApiLink()}${props.link}/${page}/${pageLimit}`, {
            params: {
                user_id: (user_id !== "") ? user_id : null
            }
        }).then((response) => {
            if (response.data.success) {
                ui = response.data.message.data.map((i, k_index) => {
                    let tags = i.tags.map(i_t => i_t.tag)
                    console.log({ i })
                    return (
                        <div key={(i.article_id)}>
                            <Card
                                className="mb-4"
                                type={1}
                                data={{
                                    key: k_index,
                                    article_id: i.article_id,
                                    title: i.title,
                                    time: i.created_on,
                                    content: i.preview,
                                    bannerLink: i.article_banner,
                                    bookmarked: i.bookmarked,
                                    liked: i.liked,
                                    likeCount: i.likes,
                                    tags: tags,
                                    user: {
                                        user_id: (i.author_details) ? i.author_details.user_id : response.data.message.user.user_id,
                                        imageLink: (i.author_details) ? i.author_details.profile_image_link : response.data.message.user.profile_image_link,
                                        name: (i.author_details) ? i.author_details.fullname : response.data.message.user.fullname,
                                        username: (i.author_details) ? i.author_details.username : response.data.message.user.username
                                    }
                                }} />
                        </div>
                    )
                })



                let latest_snapshot = articles
                if (page > 0)
                    setLoading(false)

                setLastLoad(Date.now())

                if (response.data.message.data.length > 0) {
                    setArticles((page === 0) ? ui : [...latest_snapshot, ...ui])
                    setInitWaypoint(true)
                    setNoNewArticle(false)
                }
                else {
                    if (page === 0)
                        setArticles([])
                    console.log("No new latest artile data")
                    setStopLoding(true)
                    setNoNewArticle(true)
                }
            }
        }).catch((e) => {
            console.log(e)
            setLoading(false)
            setLastLoad(Date.now())
            setStopLoding(true)
        })
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [page])

    useEffect(() => {
        getArticle()
    }, [getArticle])


    const handleWaypointEnter = () => {
        // alert("w ENTERED " + props.link)
        console.log("AT PAGE: " + page)
        if ((stopLoding && Date.now() - lastLoad >= 2000)) {
            setPage(page)
            getArticle()
            setNoNewArticle(false)
            setStopLoding(false)
        } else if (!(stopLoding)) {
            setPage(page + 1)
            setNoNewArticle(false)
            setStopLoding(false)
        }
    }


    return (
        <>
            {articles}

            {(isLoading) ?
                <>
                    <div className="w-100"
                        style={{
                            display: "grid",
                            placeItems: "center",
                            height: "120px"
                        }}>
                        <br />
                        <Loader className="loader" />
                        <br />
                    </div>
                </>
                : <></>}

            {(noNewArticle) ?
                <>
                    <p className="w-100 text-center">{(props.message) ? props.message : "No new article at the moment!"}</p>
                </> : <></>}

            {(initWaypoint) ?
                <Waypoint
                    onEnter={() => { handleWaypointEnter() }}
                // onLeave={() => { alert("left") }}
                />
                : <></>}

        </>
    );
}

export default LayoutLoader;