import React, { useEffect } from 'react';

import { Link, useLocation } from "react-router-dom";

import { HomeRounded, SearchRounded, CreateRounded, PersonRounded } from '@material-ui/icons';

// import { AuthContext } from "../../contexts/auth_context"
const BottomNav = (props) => {

    let location = useLocation()
    // const { isLoggedIn } = useContext(AuthContext)
    const [activeTab, setActiveTab] = React.useState(0)
    
    useEffect(() => {

        console.log("update location")
        if (location.pathname === "/" && activeTab !== 1)
            setActiveTab(1)

        else if (location.pathname === "/search" && activeTab !== 2)
            setActiveTab(2)

        else if (location.pathname === "/newArticle" && activeTab !== 3)
            setActiveTab(3)

        else if ((location.pathname === "/dashboard" || location.pathname === "/login" || location.pathname === "/register")
            && activeTab !== 4)
            setActiveTab(4)
        else
            setActiveTab(0)

    }, [location.pathname])

    function getIcon(type = 1) {
        if (type === 1) {
            if (activeTab === 1) {
                return (<HomeRounded className="bt-nav__icon active" />)
            } else {
                // return (<HomeOutlined className="bt-nav__icon" />)
                return (<HomeRounded className="bt-nav__icon" />)
            }
        } else if (type === 2) {
            if (activeTab === 2) {
                return (<SearchRounded className="bt-nav__icon active" />)
            } else {
                // return (<SearchOutlined className="bt-nav__icon" />)
                return (<SearchRounded className="bt-nav__icon" />)
            }
        } else if (type === 3) {
            if (activeTab === 3) {
                return (<CreateRounded className="bt-nav__icon active" />)
            } else {
                // return (<CreateOutlined className="bt-nav__icon" />)
                return (<CreateRounded className="bt-nav__icon" />)
            }
        } else if (type === 4) {
            if (activeTab === 4) {
                return (<PersonRounded className="bt-nav__icon active" />)
            } else {
                // return (<PersonOutlined className="bt-nav__icon" />)
                return (<PersonRounded className="bt-nav__icon" />)
            }
        }
    }

    function handleClick(n) {
        // do nothing
        // setActiveTab(n)
    }

    // if (isLoggedIn) {
    return (
        <>
            <div className="bt-nav">
                <div className="bt-tabs">
                    <ul className="bt-nav__ul ml-4 mr-4">
                        <li className="bt-nav__item" onClick={() => { handleClick(1) }}>
                            <Link to="/" className="bt-nav__link">
                                {getIcon(1)}
                            </Link>
                        </li>
                        <li className="bt-nav__item" onClick={() => { handleClick(2) }}>
                            <Link to="/search" className="bt-nav__link">
                                {getIcon(2)}
                            </Link>
                        </li>
                        <li className="bt-nav__item" onClick={() => { handleClick(3) }}>
                            <Link to="/newArticle" className="bt-nav__link">
                                {getIcon(3)}
                            </Link>
                        </li>
                        <li className="bt-nav__item" onClick={() => { handleClick(4) }}>
                            <Link to="/dashboard" className="bt-nav__link">
                                {getIcon(4)}
                            </Link>
                        </li>
                    </ul>
                </div>
            </div>
        </>
    );
    // } else {
    //     return (<>

    //         <div className="bt-nav">
    //             <div className="bt-tabs">
    //                 <ul className="bt-nav__ul ml-4 mr-4">
    //                     <li className="bt-nav__item" onClick={() => { handleClick(1) }}>
    //                         <Link to="/" className="bt-nav__link">
    //                             {getIcon(1)}
    //                         </Link>
    //                     </li>
    //                     <li className="bt-nav__item" onClick={() => { handleClick(2) }}>
    //                         <Link to="/search" className="bt-nav__link">
    //                             {getIcon(2)}
    //                         </Link>
    //                     </li>
    //                     <li className="bt-nav__item" onClick={() => { handleClick(4) }}>
    //                         <Link to="/login" className="bt-nav__link">
    //                             {getIcon(4)}
    //                         </Link>
    //                     </li>
    //                 </ul>
    //             </div>
    //         </div>
    //     </>)
    // }
}

export default BottomNav;