import React, { useState } from 'react';


import { Tabs, Tab } from "react-bootstrap"
import { Link } from "react-router-dom"
import { Helmet } from 'react-helmet'
import ListLayout from '../components/cardListLayoutLoader'
import Breakpoint from '../components/breakpoint/breakpoint'
const Home = () => {

    const [key, setKey] = useState('tab2');

    return (
        <>
            <Helmet>
                <meta charSet="utf-8" />
                <title>Devorzo - Your awesome dev community</title>
                <link rel="canonical" href="https://devorzo.github.io/" />
            </Helmet>

            <div className="container-xl">
                <div className="mt-2">
                    <div className="row">

                        <div className="col-md-2">
                            <Breakpoint md lg xl>
                                <div className="mt-3" style={{
                                    // position: "fixed"
                                }}>
                                    <Link to="/newArticle" className="btn btn-primary">New post</Link><br />

                                </div>
                            </Breakpoint>
                        </div>

                        <div className="col-md-8">
                            <div className="tab-section mt-3">
                                <Tabs
                                    variant="tabs"
                                    id="controlled-tab-example"
                                    activeKey={key}
                                    transition={false}
                                    onSelect={(k) => setKey(k)}>

                                    <Tab eventKey="tab1" title="Featured">
                                        <ListLayout
                                            link="/api/v1/getFeaturedArticle" />
                                    </Tab>
                                    <Tab eventKey="tab2" title="Latest">
                                        <ListLayout
                                            link="/api/v1/getLatestArticle" />
                                    </Tab>
                                    <Tab eventKey="tab3" title="Popular">
                                        <ListLayout
                                            link="/api/v1/getPopularArticle" />
                                    </Tab>
                                    {/* <Tab eventKey="tab4" title="Recommended">
                                        <ListLayout
                                            link="/api/v1/getLatestArticle" />
                                    </Tab> */}
                                </Tabs>
                            </div>
                        </div>
                        <div className="col-md-2">

                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default Home;